.card {
    width: 100px;
    height: 150px;
    margin: 1rem;
    position: relative;
    -webkit-box-shadow: 4px 4px 10px #0B132B;
            box-shadow: 4px 4px 10px #0B132B;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }

  .img-container{
    border: 4px #CCDBDC;
    border-radius: 4px;
  }

  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background: #ffffff;
  }
  
  .card > .img-container > img {
    height: 100%;
  }
  
  .card:hover {
    -webkit-box-shadow: 8px 8px 20px #0B132B;
            box-shadow: 8px 8px 20px #0B132B;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
        -ms-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }


